* {
  box-sizing: border-box;
  color: white;
  margin: 2px;
  padding: 0;
  text-align: center;
}

body {
  align-items: center;
  background-image: url('./images/background.png');
  display: flex;
  flex-direction: column;
  font-family: Epilogue , Helvetica , Arial , sans-serif;
  font-size: 100%;
  justify-content: space-between;
  line-height: 1.3;
}

h1 {
  font-size: 400%;
}

main > div {
  align-items: center;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}

.header-img {
  max-width: 100%;
}

.title {
  font-size: 40px;
  font-weight: 300;
}

.solar-system {
  display: flex;
  flex-direction: column;
}

.planets {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}

.planets > div {
  width: 130px;
}

.planets > div > img {
  width: 100%;
}

.missions-box {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 30px;
  margin: 0 5%;
}

.missions {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  min-height: 900px;
  padding-top: 10px;
  width: 90%;
}

.missions > div {
  align-items: flex-start;
  border: 1px solid #d0d7de;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  height: 175px;
  padding: 30px 15px 0;
  width: 300px;
}

.missions > div > :first-child {
  border-bottom: 1px solid #d0d7de;
  font-size: large;
  font-weight: 700;
  padding-bottom: 10px;
  text-align: left;
  width: 99%;
}

.missions-infos {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: space-around;
  width: 100%;
}

.info-div {
  align-items: center;
  display: flex;
  max-width: 30%;
}

.icon-info-div {
  height: 15px;
}
